[type="file"] {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    white-space: nowrap;
    width: 2px;
  }
   
  [type="file"] + label {
    background-color: #000;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    padding-left: 2rem 4rem;
  }
    
  [type="file"]:focus + label,
  [type="file"] + label:hover {
      background-color: #feb236;
  }
    
  .error{
    align-items: center;
    display: flex;
    margin-left: 150px;
