    transition: all 0.5s; 
    cursor: pointer;
    margin: 5px;

    background-color: #32465a;
    border: none;
    color: white;
    padding: 10px 28px ;
    width: 100%;
    text-align: center;
    font-size: 30px;
    margin-top: 40px;
   
    
   
  }
  
  .button1 span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button1 span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button1:hover span {
    padding-right: 25px;
  }
  
  .button1:hover span:after {
    opacity: 1;
    right: 0;
  }
