.homeitem1{
    display: flex;
    flex-direction: column;
    align-items: center;
    color:white;
    width:100%;
    box-shadow: 0 5px 5px #808888; 
  }
.root{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: '#fff';
    z-index: 10;
  }
  .homerightcomponent{
    box-shadow: 0 80px 80px #808888;
    
    background-color: rgb(255,209,220);
  }
  .homeleftimage{
    width:100%;
    height: 100%;
    background-color: rgb(78, 30, 78);

    
    opacity:0.6;
  }
  
  .imagehome{
      /* background-image: url(../../images/pexels-evie-shaffer-2395251.jpg); */
      background-image: url(../Images/pexels-cristian-dina-1851415.jpg);
      background-repeat: no-repeat;
  
      background-size: cover;
      background-position: center;
      /* opacity:0.5; */
      
  }
  .button1 {
    border-radius: 4px;
    width: 300px;
    transition: all 0.5s; 
    cursor: pointer;
    margin: 5px;

    background-color: #32465a;
    border: none;
    color: white;
    padding: 10px 28px ;
    width: 100%;
    text-align: center;
    font-size: 30px;
    margin-top: 40px;

   
  }
  .btnhome{
    cursor: pointer;
    background-color: pink;
    border:none;
    color:white;
    padding: 10px 28px;
    text-align: center;
    width:100%;
    font-size: 30px;
  }
  .button1 span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button1 span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button1:hover span {
    padding-right: 25px;
  }
  
  .button1:hover span:after {
    opacity: 1;
    right: 0;
  }
  .error{
    align-items: center;
    display: flex;
    margin-left: 150px;
  }
